import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';

/**
 * Generated class for the MySurveyListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-my-survey-list',
  templateUrl: 'my-survey-list.html',
})
export class MySurveyListPage {

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public modalCtrl: ModalController
    ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MySurveyListPage');
  }

  goToForm() {
    let modal = this.modalCtrl.create("AddSurveyPage",{

    }, {
      enableBackdropDismiss: true,
      showBackdrop:true,
      // cssClass:'my-modal2' 
    });
    modal.present();
  }

}



